*{
    font-family: 'Orbitron', sans-serif;
}
.backdrop{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: #000000e1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.modal{
    width: clamp(50%, 700px, 90%);
    height: min(50%, 300px);

    margin: auto;
    padding: 0 2rem;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 20;
}

.profiles-container{
    margin: 20px;
    padding: 30px;
    background-color: #000;
    border: solid 2px #4ad3e1;
    border-top: solid 20px #4ad3e1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.profile{
    margin: 20px;
    padding: 30px;
    align-items: center;
    justify-content: center;
   

}
.profile h1{
    color: #fff;
}


.hero{
    justify-content: center;
    height: 100vh;
    align-items: center;
    background-image: url('../assets/Border.png\ -\ Copy.png');
    background-repeat: no-repeat;
    background-size:contain;
    background-position: center;
    border: solid 2px #4ad3e1;
  
    margin: 0px 50px 10px 50px;;
    padding: 20px;

}
.hero h2{
    color: #fff;
}
.notice{
    padding: 20px;
    margin: auto;
    margin-top: 200px;
    justify-content: center;
    display: block;
    
    border: solid 2px #4ad3e1;
    border-top: solid 20px #4ad3e1;
    background-color: #000;
    text-align: center;
    width: 20%;
    
    

}
.btn{
    margin: 20px;
   

}

@media screen and (max-width:1240px){
    .notice{
        padding: 10px;
        margin: auto;
        margin-top: 100px;
        justify-content: center;
        display: block;
        
        border: solid 2px #4ad3e1;
        border-top: solid 20px #4ad3e1;
        background-color: #000;
        text-align: center;
        width: 40%;
        
    }
    .notice h1{
        font-size: 10px;
    }

    .modal{
        width: clamp(30%, 200px, 40%);
        height: min(30%, 200px);
    
        margin: auto;
        padding: 0 2rem;
        border-radius: 12px;
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 20;
    }
    
    .profiles-container{
        margin: 10px;
        padding: 20px;
        background-color: #000;
        border: solid 2px #4ad3e1;
        border-top: solid 20px #4ad3e1;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .profile{
        margin: 10px;
        padding: 20px;
        align-items: center;
        justify-content: center;
       
    
    }
    .profile h1{
        color: #fff;
    }
    
}